.App{
  min-width: 100vw;
  min-height:100vh;
  background-color: rgb(236, 236, 236);
}

.container{
  width: 720px;
  margin: 0 auto;
  padding-top: 100px;
}

.generator{
  background-color: rgb(255, 255, 255);
  border-radius: 3px;
  box-shadow: 0px 2px 10px rgbs(255, 255, 255, 0.2);
  padding: 20px;
}

.generator_header{
  text-align: center;
  color: rgb(0, 0, 0);
  margin-bottom: 20px;
  font-size: 30px;
}

.generator_password{
  position: relative;
  background: rgba(201, 201, 201, 0.4);
  padding: 13px 10px;
  color: rgb(0, 0, 0);
  height: 46px;
  margin-bottom: 15px;
}

.copy_btn {
  position: absolute;
  background: #18a1ca;
  color: white;
  border: none;
  height: 40px;
  padding: 10px;
  cursor: pointer;
  top: 3px;
  right: 3px;
}

.generator_btn{
  background:  #148aad;
  border: solid 4px #a2e7fc;
  display: block;
  width: 100%;
  padding: 10px;
  color: rgb(255, 255, 255);
  font-size: 25px;
  cursor: pointer;
  border-radius: 10px;
}


.form-group{
  display: flex;
  justify-content:space-between;
  color: rgb(17, 16, 16);
  margin-bottom: 15px;
}

